import { useEffect, useState } from "react"
import { useSelector, useDispatch } from "react-redux"
import VacationsList from "../../../asyncfunction/getVacations"
import { Form } from "react-bootstrap"
import SortVacations from "../../../asyncfunction/sortVacations"
import RefreshIcon from '@material-ui/icons/Refresh';
import VacationCard_upgrade from "../../ui-containers/vacationCard_upgrade"
import SearchVacation from "../../ui-containers/searchVaction"

export default function HomePageUsers() {
    const [data, setData] = useState([])
    const [refresh, setRefresh] = useState([false])
    const selector = useSelector(state => state)
    const dispatch = useDispatch()

    useEffect(async () => {
        const result = await VacationsList(selector.userId)
        dispatch({ type: "vacations data", payload: result })
        setData(result)
    }, [refresh])

    useEffect( () => {
        setData(selector.vacationsData)
    }, [selector.vacationsData])

    async function sortData(status) {
        if (status) {
            const result = await SortVacations()
            setData(result)
        } else
            setData(selector.vacationsData)
    }

    return (
        <div style={{ marginTop: "10px" }}>
            <div className="row">
                <div className="col-xl-10 col-md-10 col-6" style={{ display: "flex", justifyContent: "center" }}>
                    <Form.Group >
                        <Form.Check label="sort by vacations date" type="checkbox" onChange={(e) => { sortData(e.target.checked) }} />
                    </Form.Group>
                </div>
                <div className="col-xl-2 col-md-2 col-6" style={{ textAlign: "right", paddingRight: "10px", cursor: "pointer" }} onClick={() => {
                    setRefresh(!refresh)
                }}>
                    refresh
                    <RefreshIcon fontSize="large" />
                </div>

            </div>
            <div className="row">
                <div className="col-xl-9 col-md-7 col-12">
                    {data.map((oneVacation) => { return <VacationCard_upgrade key={oneVacation.id} info={oneVacation} /> })}
                </div>
                <div className="col-xl-3 col-md-5 col-12" style={{ paddingLeft: "30px", paddingRight: "30px" }}>
                    <SearchVacation callback={data => setData(data)} />
                </div>
            </div>
        </div>

    )
}
