import React from 'react';
import { makeStyles } from '@material-ui/core/styles'
import moment from "moment"
import { useSelector, useDispatch } from "react-redux"
import { BsTrashFill, BsPencil } from 'react-icons/bs';
import DeleteVacation from "../../asyncfunction/deleteVacationByadmin";
import { useHistory } from "react-router-dom"
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardMedia from '@material-ui/core/CardMedia';
import CardContent from '@material-ui/core/CardContent';
import Avatar from '@material-ui/core/Avatar';
import Typography from '@material-ui/core/Typography';
import { red } from '@material-ui/core/colors';


const useStyles = makeStyles((theme) => ({
    root: {
        maxWidth: 345,
    },
    media: {
        height: 0,
        paddingTop: '56.25%', // 16:9
    },
    avatar: {
        backgroundColor: red[500],
    },
    title: {
        fontSize: 100
    }
}));

export default function AdminCard_upgrade(props) {
    const { info } = props;
    const classes = useStyles();
    const userId = useSelector(state => state.userId)
    const history = useHistory()
    const dispatch = useDispatch()
    const check_in_date = moment(info.check_in_date).format("DD/MM/YYYY")
    const check_out_date = moment(info.check_out_date).format("DD/MM/YYYY")

    function callEditFunction() {
        dispatch({ type: "current vacation", payload: info })
        history.push(`/editVacation`)
    }

    return (
        <div className="col-12 onMouseCard" style={{ padding: "20px", paddingLeft: "60px" }}>
            <Card  style={{ height: "100%", padding: "20px", border: "1px solid rgb(173, 180, 177)" }}>
                <div className="row">
                    <div className="col-xl-8 col-6" >
                        <div className="row"  >
                            <div className="col-xl-3 col-6">
                                <div>
                                    <BsTrashFill style={{ fontSize: "25px", cursor: "pointer" }} onClick={() => { DeleteVacation(info.id, userId) }} />
                                    <BsPencil style={{ marginLeft: "20px", fontSize: "25px", cursor: "pointer" }}
                                        onClick={callEditFunction} />
                                </div>

                                <div className="card_price">
                                    {info.price} ₪

                                </div>
                            </div>
                            <div className="col-xl-9 col-6" style={{ textAlign: "right" }}>
                                <div className="card_title">
                                    {info.hotel_name}
                                </div>
                                <div className="card_location">
                                    {info.destination}

                                </div>
                                <div>
                                    {info.description}
                                </div>
                                <Typography style={{ paddingTop: "20px" }} variant="body1" color="textSecondary" component="p">
                                    {moment(info.check_in_date).format("DD/MM/YYYY")} - {moment(info.check_out_date).format("DD/MM/YYYY")}
                                </Typography>
                                <div className="cancelation">
                                    ביטול חינם - אין צורך במקדמה

                                </div>
                            </div>
                        </div>
                    </div>

                    <div className="col-xl-4 col-6">
                        <CardMedia
                            className={classes.media}
                            image={info.image}
                        />
                    </div>
                </div>
            </Card>
        </div>

    )
}

